 #wptravel-block-trip-calendar.wp-travel-calendar-view {
    padding: 0 0;
    background: transparent;
    
    .react-datepicker__header__dropdown{
        display: flex;
    }
    .wp-travel-booking__pricing-wrapper.wptravel-pax-selector{
        display: block;
        margin-top: 5px;
    }
    .wp-travel-booking__datepicker-wrapper {
        margin-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
        position: relative;
    }
 }
 
 